import store from "@/store";

window.bridge = {
  // 
  setStatusBar(height) {
    store.commit("setStatusBarHeight", height);
  }
};

// react-native 
window.document.addEventListener("message", (e) => {
  // window.ReactNativeWebView.postMessage(`copy that: ${e.data}`); //  RN
  const data = JSON.parse(e.data);
  const key = Object.keys(data);
  const fn = window.bridge[key];
  if (fn) {
    fn(data[key]);
  }
});

//  app 
export function setBarStyle(to) {
  if (!window.ReactNativeWebView) return;
  const whiteRoutes = ["/assets", "/exchangeSLG", "/help"];
  const style = whiteRoutes.includes(to.path) ? "light-content" : "dark-content";
  window.ReactNativeWebView.postMessage(JSON.stringify({
    fn: "setBarStyle",
    data: {
      style
    }
  }));
}

