export default {
  props: {
    pullingDown: {
      type: Boolean,
      default: false
    },
    pullingUp: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    options() {
      return {
        pullDownRefresh: this.pullDownRefreshObj,
        pullUpLoad: this.pullUpLoadObj,
        scrollbar: false
      };
    },
    pullDownRefreshObj: function () {
      return this.pullingDown ? {
        threshold: 60, // 
        stopTime: 600, // 
        txt: ""
      } : false;
    },
    pullUpLoadObj: function () {
      return this.pullingUp ? {
        threshold: 0, // 
        txt: {
          more: "",
          noMore: ""
        }
      } : false;
    }
  },
  methods: {
    onPullingDown() {
      const onPullingDown = this.$listeners.onPullingDown;
      if (onPullingDown && typeof onPullingDown === "function") onPullingDown(this.forceUpdateScroll);
    },
    onPullingUp() {
      const onPullingUp = this.$listeners.onPullingUp;
      if (onPullingUp && typeof onPullingUp === "function") onPullingUp(this.forceUpdateScroll);
    },
    // 
    refreshScroll() {
      this.$nextTick(() => {
        this.$refs.scroll.refresh();
      });
    },
    //  scroll 
    forceUpdateScroll() {
      this.$refs.scroll.forceUpdate();
      setTimeout(() => {
        this.refreshScroll();
      }, this.pullDownRefreshObj.stopTime);
    }
  },
  mounted() {
    this.refreshScroll();
  },
  activated() {
    this.refreshScroll();
  }
};
