import axios from "axios";
// import {Message} from "element-ui";
import router from "@/router";
const CancelToken = axios.CancelToken;
const source = CancelToken.source();

const $axios = axios.create({
  baseURL: process.env.VUE_APP_URL,
  withCredentials: true,
  timeout: 10000,
  headers: {}
});

$axios.interceptors.request.use(config => {
  //  content-type  post  options 
  // if ((config.method).toLowerCase() === "post") config.headers["Content-Type"] = "application/x-www-form-urlencoded";
  config.cancelToken = source.token; // cancelToken
  return config;
}, err => {
  return Promise.reject(err);
});

// 
$axios.interceptors.response.use((response) => {
  // 
  if (response.data.flag === false && response.data.auth === false) {
    source.cancel(); // 
    // Message.error(response.data.message);
    router.push("/login");
    sessionStorage.removeItem("username");
    return Promise.reject(response);
  }
  return response;
}, (error) => {
  console.dir(error);
  // if (error.message) Message.error(error.message);
  // if (axios.isCancel(error)) return new Promise(() => {}); // Promise
  // 
  return Promise.reject(error);
});

window.axios = $axios;
export default $axios;
